import { InjectionToken } from '@angular/core';
import { Observable } from 'rxjs';

export interface ComboHttpService {
    /**
     * ID
     * @param uri ID or Rest API
     * @param data  key/value
     */
    context?: any;
    getData(uri: string, params?: any, method?: string): Observable<any>;
}
export const ComboServerSideToken = new InjectionToken<ComboHttpService>('Combo Grid HTTP service');
